.footer {
    padding: calc($separacion / 2);
    background-color: $verde;
    height: 150px;
    position: absolute; 
    left: 0; 
    bottom: 0; 
    width: 100%;
   
   

    .contenedor-footer {
        padding-top: calc($separacion / 2);
    }

    .navegacion {
        visibility: visible;
        opacity: 1;
        height: auto;
    }

    .copyright {
        color: $blanco;
        text-align: center;
    }
}

