.derecha {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet {
        align-items: flex-end;
    }

    .dark-mode-boton {
        width: 3rem;
        margin-top: 3rem;
        margin-bottom: 2rem;
        filter: invert(100%);

        &:hover {
            cursor: pointer;
        }

        @include tablet {
            margin-top: 0;
        }
    }
}

.barra {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 3rem;
    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: calc(1/2);

    }
}

.navegacion {

    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity .5s linear, height .5s linear; 
    
    @include tablet {
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;
    }
    
    a {
        color: $negro;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;
        

        @include tablet {
            margin-right: 2rem;
            font-size: 2.2rem;
            font-weight: bold;

            &:last-of-type {
                margin-right: 0;
            }
        }

        &:hover {
            color: $blanco;
        }
    }
}


/** Menu de hamburguesa **/
.mobile-menu {
    @include tablet {
        display: none;
    }

    img {
        width: 5rem;
    }
}

.mostrar {
    visibility: visible;
    opacity: 1;
    height: auto;
}

.menu-horizontal{
	list-style-type: none;
    padding: 0;
	display: flex;
    flex-flow: column;
	justify-content: space-around;
    background-color: rgba(0, 0, 0, .5);
    align-items: center;
    width: 200px;
    margin-bottom: 14rem;
    
    a{
        font-size: 2rem;
    }


    @include tablet{
        flex-flow: row;
        width: fit-content;
        margin-bottom: 0rem;
    }
    

    
}
.menu-horizontal > li > a{
	display: flex;
	padding: 15px 20px;
	color: white;
	text-decoration: none;
}
.menu-horizontal > li:hover{
	background-color: red;
}
.menu-vertical{
	position: absolute;
	display: none;
	list-style-type: none;
    padding: 0;
	width: 200px;
	background-color: rgba(0, 0, 0, .5);
}
.menu-horizontal li:hover .menu-vertical{
	display: block;
}
.menu-vertical li:hover{
	background-color: black;
}

.menu-vertical li a{
	display: block;
	color: white;
	text-decoration: none;
	padding: 15px 15px 15px 20px;
}


