:root{
    --facebook: #3b5999;
    --instagram: #e4405f;
    --twitter: #55acee;
}

@include tablet {
    .iconos-nosotros {
        @include grid(3, 2rem);
    }
}

.icono {
    text-align: center;
    img {
        height: 10rem;
    }
    h3 {
        text-transform: uppercase;
    }
}

.redes-container{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 4rem;
    background-color: #333;
    margin-bottom: 0px;
    color: #fff;
}


.redes-container ul li{
    display: inline-block;
    margin: 0 5px;
    width: 50px;
    height: 50px;
    text-align: center;
}

.redes-container ul li a{
    display: block;
    padding: 0 10px;
    background: #333;
    color: #fff;
    line-height: 50px;
    font-size: 18px;
    box-shadow: 0 3px 5px 0px rgba(0, 0, 0, .2);
    transition: .2s;
}

.redes-container ul:hover a{
    filter: blur(2px);
}

.redes-container ul li a:hover{
    transform: translateY(-3px) scale(1.05);
    box-shadow: 0 5px 5px 0px rgba(0, 0, 0, .4);
    filter: none;
}

.redes-container ul li .facebook:hover{
    background: var(--facebook);
}
.redes-container ul li .instagram:hover{
    background: var(--instagram);
}

.redes-container ul li .twitter:hover{
    background: var(--twitter);
}

.redes-container p{
    color: #fff;
}

.enlaces-footer{
    display: flex;
    flex-flow: column;
    align-items: center;

    a{
        color: #fff;
        font-size: 2rem;
    }
}

.textos{
    text-align: justify;
}

.imagen {
    
    display: flex;
    justify-content: center;
    justify-items: center;
    align-items: center;
}

.titulo{
    text-align: center;
}


